import React, { PureComponent } from "react";
import { connect } from 'react-redux';
import { isEmbed } from "../../../actions/api";
const ReviewHub = React.lazy(() => import('./ReviewHub'));

class UserComponents extends PureComponent {
  render() {
    const { modalPop, appData } = this.props
    return modalPop.onSiteIsShowPopUp && appData.wall && Object.keys(appData.wall).length && isEmbed ? <ReviewHub wall={appData.wall} wallId={appData.wallID} feeds={appData.feeds} isFreeAdsStatus={appData.postData.isFreeAdsStatus} /> : '' 
  }
}

const mapStateToProps = state => {
  return {
    modalPop: state.modalPop,
    appData: state.appData,
    loaderData: state.loaderData
  }
}

export default connect(mapStateToProps)(UserComponents);

